<template>
  <fade-transition>
    <div class="alert" :class="[`alert-${type}`]" role="alert" v-if="visible">
      <slot v-if="!dismissible"></slot>
      <div v-else class="container">
        <slot></slot>
        <slot name="dismiss-icon">
          <button
            type="button"
            class="close"
            aria-label="Close"
            @click="dismissAlert"
          >
            <span aria-hidden="true">
              <i class="now-ui-icons ui-1_simple-remove"></i>
            </span>
          </button>
        </slot>
      </div>
    </div>
  </fade-transition>
</template>
<script>
import { FadeTransition } from "vue2-transitions";

export default {
  name: "alert",
  components: {
    FadeTransition,
  },
  props: {
    type: {
      type: String,
      default: "default",
      description: "Alert type",
    },
    dismissible: {
      type: Boolean,
      default: false,
    },
    visible: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    dismissAlert() {
      let typeStr = this.type.toUpperCase();
      this.$store.commit(`msgStore/SET_${typeStr}_MSG`, {
        visible: !this.visible,
      });
    },
  },
};
</script>
<style scoped>
.alert {
  position: fixed;
  width: 100vw;
  bottom: 4vh;
  height: 3rem;
  font-weight: bold;
  z-index: 99;
}
</style>
